import {
  _decorator,
  Color,
  director,
  Node,
  Sprite,
  SpriteRenderer,
  Vec3,
} from "cc";
import { EffectManager } from "../Effects/EffectManager";
const { ccclass, property } = _decorator;

enum Direction {
  Horizontal,
  Vertical,
}

@ccclass("MatchChecker")
export class MatchChecker {
  private static directions = [
    { dx: 0, dy: 1 }, // Right
    { dx: 0, dy: -1 }, // Left
    { dx: 1, dy: 0 }, // Down
    { dx: -1, dy: 0 }, // Up
  ];

  public static async SwapPiecePositions(
    pieceA: Node,
    pieceB: Node,
    posA: Vec3,
    posB: Vec3,
    gridMap: Map<Node, { row: number; col: number }>
  ) {
    EffectManager.movePiece(pieceA, posB, 0.1);
    await EffectManager.movePiece(pieceB, posA, 0.1);
    // Update the gridMap
    this.highlightNeighbors(pieceA, gridMap);
    const posAData = gridMap.get(pieceA);
    const posBData = gridMap.get(pieceB);
    gridMap.set(pieceA, posBData);
    gridMap.set(pieceB, posAData);
  }

  private static highlightNeighbors(
    currentPiece: Node,
    gridMap: Map<Node, { row: number; col: number }>
  ): void {
    for (const { dx, dy } of this.directions) {
      const currentPiecePosition = gridMap.get(currentPiece);
      const neighborPiecePosition = {
        row: currentPiecePosition.row + dx,
        col: currentPiecePosition.col + dy,
      };
      //bla bla
      const neighbor = this.getPieceAtPosition(neighborPiecePosition, gridMap);
      if (neighbor) {
        if (neighbor.name === currentPiece.name) {
          const sprite = neighbor.getChildByName("Sprite").getComponent(Sprite);
          if (sprite) {
            sprite.color = new Color(150, 150, 255); // Set to white
          }
          this.highlightNeighbors(neighbor,gridMap);
        }
      }
    }
  }
  private static getPieceAtPosition(
    position: { row: number; col: number },
    gridMap: Map<Node, { row: number; col: number }>
  ): Node | undefined {
    return Array.from(gridMap.entries()).find(
      ([_, pos]) => pos.row === position.row && pos.col === position.col
    )?.[0];
  }
}
